import Services from "./Services";

export class MarketTrend extends Services {
  constructor(init) {
    super(init);
    this._name = "MARKET_TREND";
    return this;
  }

  //MARKET TREND ----------------------------------------------------------------------
  /**
   * @function  - Get market trends data from coinmarketcap
   * @param {Object} data
   * @returns
   */
  market_trend = async (data) => {
    return await this.decorate(
      async () =>
        await this.axios(
          `https://pro-api.coinmarketcap.com/v1/cryptocurrency/listings/historical`,
          {
            method: "GET",
            headers: {
              "X-CMC_PRO_API_KEY": "de0d4961-f1f3-451e-8522-d895f89a80a7",
            },
          }
        )
    );
  };
}
export default MarketTrend;
